<template>
  <div class="mine-main">
    <!-- <headerBar :header-data="headerData" /> -->
    <div :class="{'search-open-contact':$store.state.headerStatus}">
      <!-- 我的-姓名和头像 begin -->
      <div class="mine-head lineGradient">
        <div class="mine-info">
          <div class="mine-name">{{ username }}</div>
          <div class="mine-tx" @click="showImage(userInfo.tx_url?userInfo.tx_url:avatar)">
            <van-image
              :src="
                userInfo.tx_url?userInfo.tx_url:avatar"
              fit="cover"
              :round="true"
            />
          </div>

          <!-- <div class="mine-arrow">
             <i class="iconfont iconarrowright" />
          </div> -->
        </div>

        <!-- 我的-姓名和头像 end -->
        <div class="content">
          <div class="mine-list negative box">
            <van-cell class="cell-list" :value="count" is-link @click="cleanlj()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-qlhc.png')" width="20" height="20" /></div>
                <span class="cell-title">清理缓存</span>
              </template>
            </van-cell>
            <!-- <van-cell class="cell-list" is-link @click="goSysm()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-sysm.png')" width="20" height="20" /></div>
                <span class="cell-title">使用说明</span>
              </template>
            </van-cell> -->

            <van-cell class="cell-list" is-link @click="goGywm()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-gywm.png')" width="20" height="20" /></div>
                <span class="cell-title">关于我们</span>
              </template>
            </van-cell>

            <!-- 1 是 0 否 -->
            <van-cell v-if="userInfo.gxzbqx == 1" class="cell-list" is-link @click="goDzzb()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-sczb.png')" width="20" height="24" /></div>
                <span class="cell-title">上传电子坐标</span>
              </template>
            </van-cell>

            <van-cell class="cell-list" :value="version" is-link @click="goJcgx()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-jcgx.png')" width="20" height="20" /></div>
                <span class="cell-title">检查更新</span>
              </template>
            </van-cell>

            <!-- <van-cell class="cell-list" :value="version" is-link @click="goQj()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-jcgx.png')" width="20" height="20" /></div>
                <span class="cell-title">请假</span>
              </template>
            </van-cell> -->

            <!-- <van-cell class="cell-list" is-link @click="goWdzs()">
              <template slot="title">
                <div class="cell-listimg"><van-image :src="require('@/assets/images/mine/icon-wdzs.png')" width="20" height="20" /></div>
                <span class="cell-title">移动证书</span>
              </template>
            </van-cell> -->

          </div>
        </div>

        <!-- 退出按钮 begin -->
        <div class="loginout-button">
          <van-button type="default" @click="outLogin()">退出登录</van-button>
        </div>
        <!-- 退出按钮 begin -->

        <!-- 版本信息 begin -->
        <div class="mine-copyright">当前版本 {{ version }}</div>
        <!-- 版本信息 end -->
        <FooterNav />
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import { NativeCaJS, NativeJS } from '@/common/js/mixin.js'
import { mapState } from 'vuex'
import { ImagePreview } from 'vant'
import FooterNav from '@/components/footerNav/footerNav.vue'
export default {
  name: 'GrzxIndex',
  components: {
    FooterNav
  },

  mixins: [NativeCaJS, NativeJS],
  data() {
    return {
      headerData: {
        title: '个人信息',
        leftUrl: '',
        leftText: ' '
      },
      version: process.env.VUE_APP_VERSION,
      profiletitle: '我的',
      username: '登录/注册', // 用户名
      resetname: '',
      mobile: '暂无绑定手机号', // 电话号码
      balance: 0, // 我的余额
      count: '', // 优惠券个数
      pointNumber: 0, // 积分数
      avatar: require('@/assets/images/default-tx.png'), // 头像地址
      ddLogin: true
    }
  },
  computed: {
    // 取数据
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.$store.commit('router/SET_Foot_Num', 4)
    // this.avatar = this.$store.state.mh.mhUserInfo.tx_url
    this.username = this.$store.state.mh.mhUserInfo.bmmc + '/' + this.$store.state.mh.mhUserInfo.name
  },
  mounted() {
    var self = this
    window['updateLocation'] = function(value) {
      if (value !== '') {
        // self.center.push(value.lon)
        // self.center.push(value.lat)
        // self.position1.push(value.lon)
        // self.position1.push(value.lat)
        // self.cdLable = value.address
        self.$router.push({
          name: 'KqWz',
          params: { centerLon: value.lon, centerLat: value.lat,
            address: value.address }
        })
        console.info('updateLocation--ww---', self.center)
        console.info('updateLocation--ww---', self.position1)
        // self.addLatAndLng()
      } else {
        console.info('updateLocation--2---', value)
        self.$toast.fail('定位数据获取失败！')
      }
    }
    this.getlj()
  },
  methods: {
    goQj() {
      this.$router.push('QjList')
    },
    showImage(imagePath) {
      ImagePreview({
        images: [
          imagePath
        ],
        startPosition: 0,
        onClose() {
          // do something
        }
      })
    },
    // 上传电子坐标
    goDzzb() {
      const pin = {
        'type': '1'
      }
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.getDistance(JSON.stringify(pin))
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getDistance', JSON.stringify(pin), (data) => {
          const datas = JSON.parse(data)
          // var lnglat = new AMap.LngLat(117.186602, 34.261579)// 测量点
          // this.center.push(datas.lng)
          // this.center.push(datas.lat)
          // this.position1.push(datas.lng)
          // this.position1.push(datas.lat)
          // this.cdLable = datas.address
          this.$router.push({
            name: 'KqWz',
            params: { centerLon: datas.lng, centerLat: datas.lat,
              address: datas.address }
          })
        })
      } else {
        this.$toast('返回原生页面')
      }
    },

    // 我的证书
    goWdzs() {
      this.$router.push({
        name: 'CaXx'
      })
    },
    // 下载
    goXiaZai() {
      this.NativeLoadCA()
    },
    // 加密
    goJiaMi() {
      this.NativeEnvelopeDecrypt({ 'pin': '123456' })
    },
    // 信息
    goXinXi() {
      this.NativeGetAllCAInfo()
    },
    // 解密
    goJieMi() {
      this.NativeEnvelopeDecrypt({ 'pin': '123456' })
    },
    // 签章
    goSing() {
      this.$router.push({
        name: 'CaCz'
      })
    },
    outLogin() {
      this.NativeDissPush()
      this.$store.commit('mh/SET_MHUSER_INFO', '')
      this.$store.commit('mh/SET_IS_MHLOGIN', false)
      this.$store.commit('router/SET_Foot_Meun', [])
      this.$store.commit('ca/SET_CaListItem', {})
      this.$store.commit('ca/SET_CaPin', '')
      // this.$store.commit('ca/SET_CaXLH', '')
      this.$store.state.qmUrl = undefined
      console.log('退出', this.$store.state.qmUrl)
      this.$router.push({
        name: 'mhlogin'
      })
    },
    getlj() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          this.count = window.myapp.getlj()
        } catch (err) {
          console.log(err)
        }
        // try {
        //   this.count = window.webkit.getlj.getCheckState.postMessage()
        // } catch (err) {
        //   console.log(err)
        // }
      }
    },
    cleanlj() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          this.count = window.myapp.cleanlj()
        } catch (err) {
          console.log(err)
        }
        // try {
        //   this.count = window.webkit.cleanlj.getCheckState.postMessage()
        // } catch (err) {
        //   console.log(err)
        // }
      }
      this.$toast.success('缓存清理成功')
    },
    initData() {
      // if (this.isLogin) {
      // this.avatar = this.$store.state.mh.mhUserInfo.tx_url
      this.username = this.$store.state.mh.mhUserInfo.bmmc + '/' + this.$store.state.mh.mhUserInfo.name
      // this.ddLogin = false
      // this.mobile = this.userInfo.mobile || '暂无绑定手机号';
      // this.balance = this.userInfo.balance;
      // this.count = this.userInfo.gift_amount;
      // this.pointNumber = this.userInfo.point;
      // } else {
      //   this.username = '登录/注册'
      //   this.ddLogin = true
      //   // this.mobile = '暂无绑定手机号'
      // }
    },
    goQlhc() {},
    goYjfk() {},
    goSysm() {
      this.$router.push({
        name: 'GrzxSysm'
      })
    },
    goGywm() {
      this.$router.push({
        name: 'GrzxGywm'
      })
    },
    goJcgx() {
      this.$store.dispatch('erp/queryLatestVersionAction').then(res => {
        console.info('pinpinpinpinpinpinpin===1===', res)
        if (res) {
          const pin = { 'Bbh': res.data.bbh,
            'flag': 'grzx',
            'sfqzgx': res.data.sfqzgx,
            'gxnr': res.data.bbms,
            'xzdz': res.data.qlwjurl
          }
          console.info('pinpinpinpinpinpinpin===2===', res)
          if (process.env.NODE_ENV !== 'development') {
            try {
              console.info('NativeGetAllCAInfo======')
              window.myapp.checkUpdate(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('cheakUpdate', '', (data) => {
            })
          }
        }
      })
    },
    goqyxx() {
      // if (this.isLogin) {
      this.$router.push({
        name: 'GrzxGrxx'
      })
      // }
      // else {
      //   this.$router.push({
      //     name: 'login'
      //   })
      // }
    }
  }
}
</script>

<style lang="css" scoped>
.mine-list{
  margin-top: 50px;
}
</style>
